@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400&display=swap);
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width:800px) {
  h1 {
      font-size:  55px;
     }
