* {
    box-sizing: border-box;
}

body {
   background-color: #fff;
   margin: 0px;
   padding: 0px;
   color: #000;
   font-family: 'Comfortaa', cursive;
   font-weight: 300;
}

h1 {
    margin: 10px;
}



p {
    margin: 0;
    padding: 0;
}



.Calc {
    margin-top: 70px;
    width: 80%;
    padding: 5px;
    font-size: 40px;
    text-align: center; 
}
.Calc input {
    margin-right: 10px;
    height: 40px;
    border: none;
    outline: none;
    font-size: 24px;
    width: 120px;
}

.Calc input::-webkit-input-placeholder {
    font-size: 12px;
    font-family: 'Comfortaa', cursive;
}

.Calc input:-ms-input-placeholder {
    font-size: 12px;
    font-family: 'Comfortaa', cursive;
}

.Calc input::placeholder {
    font-size: 12px;
    font-family: 'Comfortaa', cursive;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
  }

input[type="number"]:hover,
input[type="number"]:focus {
    -moz-appearance: number-input;
  }

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.Form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.contact {
    font-size: 15px;
}

.Sell {
    
    -webkit-justify-content: space-around;
    
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 40px;
}

.Val {
    text-align: left;
    width: -webkit-max-content;
    width: max-content;
}

.Val__inner {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #000;
}

.Val__subtilte {
    font-size: 18px;
    margin-top: 5px;
}

@media (max-width:450px) {
    .Form {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }

    .Calc {
        margin-top: 30px;
    }

    .Val {
        margin: 20px 0;
    }
}
* {
    box-sizing: border-box;
    
}
.Col1 {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0 auto;
}

.Card {
    width: -webkit-max-content;
    width: max-content;
    font-size: 24px;
    margin-left: 30px;
    text-align: left;
    cursor: pointer;
    border: none;
    background: none;
    padding: 0;
    font-family: 'Comfortaa', cursive;
}

@media (max-width:450px) {
    .Card  {
        font-size: 18px;
    }
}

.Header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.Header h1 {
    margin: 80px 20px;
    font-size: 96px;
}

@media (max-width:800px) {
    .Header h1 {
        font-size:  55px;
       }
}


@media (max-width:450px) {
    .Header h1 {
        font-size:  40px;
        margin: 0;
       }
    .Header {
        padding: 40px 0px;
    }
}
* {
    box-sizing: border-box;
    
}

.Col0 {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    margin: 0 auto;
    -webkit-animation-name: line-scroll ;
            animation-name: line-scroll ;
    -webkit-animation-duration: 25s;
            animation-duration: 25s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.Line {
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    width: 70%;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    height: 40px;
    margin: 0 auto;
    overflow-x: hidden;
    padding: 20px 0;
    margin-bottom: 40px;
}

.Card__line {
    white-space: nowrap;
    width: 100%;
    font-size: 24px;
    margin-left: 30px;
    text-align: left;
    padding: 0;
}

@-webkit-keyframes line-scroll {
    0% {
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

@keyframes line-scroll {
    0% {
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

.red {
    color: rgb(110, 18, 18);
}

.green {
    color: #246C27;
}

@media (max-width:800px) {
    @-webkit-keyframes line-scroll {
        0% {
            -webkit-transform: translateX(20%);
                    transform: translateX(20%);
        }
        100% {
            -webkit-transform: translateX(-100%);
                    transform: translateX(-100%);
        }
    }
    @keyframes line-scroll {
        0% {
            -webkit-transform: translateX(20%);
                    transform: translateX(20%);
        }
        100% {
            -webkit-transform: translateX(-100%);
                    transform: translateX(-100%);
        }
    }
}

@media (max-width:450px) {
    .Line {
        width: 100%;
    }
    @-webkit-keyframes line-scroll {
        0% {
            -webkit-transform: translateX(10%);
                    transform: translateX(10%);
        }
        100% {
            -webkit-transform: translateX(-100%);
                    transform: translateX(-100%);
        }
    }
    @keyframes line-scroll {
        0% {
            -webkit-transform: translateX(10%);
                    transform: translateX(10%);
        }
        100% {
            -webkit-transform: translateX(-100%);
                    transform: translateX(-100%);
        }
    }
}
.Main {
    height: 100vh;
}

.AllVals {
    width: 100%;
    margin: 0 auto;
}


.Row1 , .Row2  {
display: -webkit-flex;
display: flex;
border: 1px solid #000;
border-radius: 50px;
-webkit-align-items: center;
        align-items: center;
