font-size: 24px;
overflow-x: scroll;
width: 90%;
margin: 0 auto;
margin-bottom: 25px;
}

::-webkit-scrollbar {
    display: none;
}

.Row1::before {
    content: 'Продать';
    background-color: #000;
    color: #fff;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 20px;
    border-radius: 50px 0 0 50px;
}



.Row2::before {
    content: 'Купить';
    background-color: #000;
    color: #fff;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 20px;
    border-radius: 50px 0 0 50px;
}


.Col2 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    text-align: center;
}

@media (max-width:450px) {
    .Row1 , .Row2  {
        width: 95%;
        font-size: 18px;
        height: 40px;
    }
}
